<template>
  <v-form ref="contactForm" :value="validContactForm">
    <v-card shaped dark class="contact-card-form">
      <v-card-title>
        <h2
          class="mt-10 mb-4 fancy--text section-title text-center primary--text"
        >
          Get In Touch
        </h2>
      </v-card-title>
      <v-card-subtitle class="text-center title">
        If you are planning an event or want more information about our
        services, please give us a call at
        <a href="tel:+16177977396">+1.617.797.7396</a> or fill out the form
        below and we'll get back to you shortly.
      </v-card-subtitle>
      <v-card-text>
        <v-text-field
          name="name"
          label="Full name"
          prepend-inner-icon="fas fa-user"
          filled
          required
        ></v-text-field>
        <v-text-field
          name="email"
          label="Email Address"
          prepend-inner-icon="fas fa-at"
          filled
          required
        ></v-text-field>
        <v-text-field
          name="phone"
          label="Phone number"
          prepend-inner-icon="fas fa-phone"
          filled
          required
        ></v-text-field>
        <v-textarea
          name="message"
          label="Your message"
          prepend-inner-icon="fas fa-pen"
          filled
          required
        ></v-textarea>
      </v-card-text>
      <v-card-actions class="mx-2 pb-4">
        <v-btn
          :loading="actionButtonLoading"
          :disabled="actionButtonDisabled"
          color="primary"
          dark
          right
          large
          outlined
          block
        >
          <v-icon left>fas fa-paper-plane</v-icon>
          Send message
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-form>
</template>

<script>
export default {
  name: "contact-form",
  data() {
    return {
      actionButtonLoading: false,
      validContactForm: true
    };
  },
  computed: {
    actionButtonDisabled: function() {
      return this.actionButtonLoading || !this.validContactForm;
    }
  }
};
</script>

<style lang="scss">
.contact-card-form {
  margin-top: 22%;
}
.section-title {
  width: 100%;
}
</style>
