      <v-list-item-avatar outlined :color="color">
        <v-icon dark v-text="icon"></v-icon>
      </v-list-item-avatar>
      <v-list-item-content>
        <v-list-item-title class="headline" v-text="title"></v-list-item-title>
      </v-list-item-content>
    </v-list-item>
    <v-img :src="image" aspect-ratio="1.7"></v-img>
